* {
  margin: 0;
  padding: 0;
  line-height: 1.5;
}

:root {
  --baseColor: white;
  --dominateColor: black;
  --accentColor:green;
}

h2 {
  color: var(--accentColor);
  text-align: center;
  line-height: 2;
  font-size: 1em;
  height: 2em;
}

section {
  margin: 1.5em;
}

article {
  padding: 1em;
}

h3 {
  margin: 1em 0;
}

article ul {
  padding-left: 1em;
}

table {
  border-collapse: collapse;
  border-spacing: none;
  width: 100%;
}

caption {
  text-align: left;
  height: 2em;
}

th, td {
  border: .1em solid var(--dominateColor);
}

thead th {
  background-color: #666;
  color: #fff;
  padding: 0.5em;
}

tbody td {
  padding: .5em;
}

.point {
  text-align: center;
}
